.form input{border-bottom:1px solid #000}.form input::placeholder{color:var(--dark-color)}.form input.form__input--warning::placeholder{color:#ff8989}.form textarea::placeholder{color:var(--dark-color)}.form .form__checkbox{display:none}.form .form__checkbox+label{display:flex;align-items:center;cursor:pointer;gap:8px}.form .form__checkbox+label::before{content:"";width:17px;height:17px;border:2px solid var(--brown);border-radius:3px;display:inline-block;transition:background .2s,border .2s}.form .form__checkbox:checked+label::before{background:var(--brown);border-color:var(--brown);position:relative}.form .form__checkbox:checked+label::before{content:"✔";color:#fff;font-size:13px;text-align:center;line-height:15px}.form__checkbox-wrapper{margin:0 auto}.form__checkbox-wrapper label{color:var(--brown);font-size:18px;line-height:1.25}.form__warning{color:#ff8989}